export default class Record {
  constructor(title, timeList) {
    let obj =
      typeof title === 'object'
        ? title
        : {
            title: title,
            timeList: timeList
          }
    this.title = obj.title || ''
    this.startTime = obj.startTime || 0
    this.relativeStr = obj.relativeStr || ''
    this.timeList = obj.timeList || []
    this.checkTimeList()
    this.createAt = obj.createAt || (this.timeList[0] || {}).start || Date.now()
  }

  checkTimeList() {
    this.timeList = this.timeList.filter(el => {
      return el.start !== 'undefined' && typeof el.end !== 'undefined'
    })
  }

  start() {
    if (!this.startTime) {
      this.startTime = Date.now()
      this.timeList.push({
        start: this.startTime,
        end: 0
      })
    }
  }

  stop() {
    this.startTime = 0
    try {
      this.timeList[this.timeList.length - 1].end = Date.now()
    } catch (err) {}
  }

  updateRelativeStr(start, end) {
    start = start || this.startTime || Date.now()
    end = end || Date.now()
    let relativeTime = end - start
    let str = ''
    let dTime = 1 * 24 * 60 * 60 * 1000
    let hTime = 1 * 60 * 60 * 1000
    let mTime = 1 * 60 * 1000
    let sTime = 1 * 1000
    if (relativeTime >= dTime) {
      let days = parseInt(relativeTime / dTime)
      relativeTime = relativeTime - days * dTime
      str += days + '天'
    }
    if (relativeTime >= hTime) {
      let hours = parseInt(relativeTime / hTime)
      relativeTime = relativeTime - hours * hTime
      str += hours + '小时'
    }
    if (relativeTime >= mTime) {
      let minutes = parseInt(relativeTime / mTime)
      relativeTime = relativeTime - minutes * mTime
      str += minutes + '分'
    }
    if (relativeTime >= sTime) {
      let seconds = parseInt(relativeTime / sTime)
      relativeTime = relativeTime - seconds * sTime
      str += seconds + '秒'
    }
    if (!str) {
      str = '0秒'
    }
    this.timeList[this.timeList.length - 1].end = end
    this.relativeStr = str
    // console.log(this)
  }
}
