/**
 * 日常
 * @module /views/Daily/index
 */

import './index.scss'
import React from 'react'
import { List } from 'antd-mobile'
import Record from '../../models/Record'
import { Local } from '../../uitls/Store'
import dayjs from 'dayjs'

const Item = List.Item

class Daily extends React.Component {
  constructor(props) {
    super(props)
    this.localKey = 'waid-index-record-list'
    this.state = {
      recordList: this.loadeRecordList()
    }
  }

  loadeRecordList() {
    let recordList = Local.get(this.localKey) || []
    let dateRecordObj = {}
    // 按天拆分每个事项记录的时间段记录
    // 只要开始时间在今天，那么整个时间段都放到今天
    recordList.forEach(el => {
      let record = new Record(el)
      record.timeList.forEach(el => {
        let date = dayjs(el.start).format('YYYY-MM-DD')
        dateRecordObj[date] = dateRecordObj[date] || []
        let recordTmp = new Record(record)
        recordTmp.timeList = [el]
        recordTmp.startTime = el.start
        recordTmp.updateRelativeStr(el.start, el.end)
        dateRecordObj[date].push(recordTmp)
      })
    })

    let dateList = []
    for (let key in dateRecordObj) {
      dateRecordObj[key].sort((el0, el1) => {
        return el0.startTime - el1.startTime
      })
      dateList.push({
        date: key,
        records: dateRecordObj[key]
      })
    }
    dateList.sort((el0, el1) => {
      return dayjs(el0.date).isAfter(dayjs(el1.date)) ? -1 : 1
    })
    // console.log(dateList)
    return dateList
  }

  render() {
    return (
      <section className="daily">
        {this.state.recordList.map((dateItem, dateIndex) => (
          <div className="dailiy-list-box" key={dateIndex}>
            <div className="daily-list-header" style={{ zIndex: dateIndex + 99 }}>
              {dateItem.date}
            </div>
            <List className="daily-list">
              {dateItem.records.map((record, index) => (
                <Item extra={record.relativeStr} key={index}>
                  {record.title}
                </Item>
              ))}
            </List>
          </div>
        ))}
      </section>
    )
  }
}

export default Daily
