/**
 * 根组件
 * @module /views/App/index
 */
import './index.scss'
import React from 'react'
import Index from '../Index/index'
import Daily from '../Daily/index'
import { HashRouter as Router, Route } from 'react-router-dom'
import { Popover, Icon } from 'antd-mobile'

const PopoverItem = Popover.Item
const popover = {
  vivible: false,
  value: ''
}

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      popover: popover
    }
  }

  handlePopoverVisibleChange(vivible) {
    popover.vivible = vivible
    this.setState({
      popover: popover
    })
  }

  onPopoverSelect(opt) {
    window.location.href = '#' + opt.props.value
    this.handlePopoverVisibleChange(false)
  }

  render() {
    return (
      <section className="app">
        <Router>
          <Route path="/" exact component={Index} />
          <Route path="/daily" exact component={Daily} />
          {/* 悬浮按钮 */}
          <Popover
            mask
            overlayClassName="app-menu"
            visible={this.state.popover.vivible}
            overlay={[
              <PopoverItem value="/">
                <div className="app-menu-item">我在做些什么？！</div>
              </PopoverItem>,
              <PopoverItem value="/daily">
                <div className="app-menu-item">日常</div>
              </PopoverItem>
            ]}
            align={{
              overflow: { adjustY: 0, adjustX: 0 },
              offset: [-10, 0]
            }}
            placement="left"
            onVisibleChange={visible => this.handlePopoverVisibleChange(visible)}
            onSelect={opt => this.onPopoverSelect(opt)}
          >
            <div className="index-more float-btn">
              <Icon type="ellipsis" size="lg" color="#666" />
            </div>
          </Popover>
        </Router>
      </section>
    )
  }
}

export default App
