/**
 * 首页
 * @module /views/Index/index
 */

import './index.scss'
import React from 'react'
import { List, Modal, InputItem, Icon } from 'antd-mobile'
import Record from '../../models/Record'
import CAction from '../../components/CAction'
import { Local } from '../../uitls/Store'

const Item = List.Item

const modal = {
  vivible: false,
  value: ''
}

class Index extends React.Component {
  constructor(props) {
    super(props)
    this.localKey = 'waid-index-record-list'
    this.state = {
      recordList: this.loadeRecordList(),
      modal: modal
    }
    this.timerID = null
  }

  componentDidMount() {
    this.start()
  }

  componentDidUpdate(prevProps, prevState) {
    this.storeRecordList()
  }

  componentWillUnmount() {
    this.stop()
  }

  start(index) {
    let recordList = this.state.recordList
    let record = recordList[index] || new Record()
    if (record.startTime) {
      record.stop()
    } else {
      record.start()
    }
    // 开始事项置顶
    if (index >= 0 && index < recordList.length && record.startTime) {
      recordList.splice(index, 1)
      recordList.unshift(record)
    }
    let _activeRecordList = []
    for (let i = recordList.length - 1; i >= 0; i--) {
      let _record = recordList[i]
      if (_record.startTime) {
        recordList.splice(i, 1)
        _activeRecordList.push(_record)
      }
    }
    _activeRecordList.forEach(el => {
      recordList.unshift(el)
    })
    let started = false
    recordList.forEach(el => {
      started = started || el.startTime
    })
    if (!started) {
      this.setState({
        recordList: recordList
      })
      this.stop()
    } else if (!this.timerID) {
      this.timerID = setInterval(() => {
        recordList.forEach(el => {
          if (el.startTime) el.updateRelativeStr()
        })
        this.setState({
          recordList: recordList
        })
      }, 1000)
    }
  }

  stop() {
    clearInterval(this.timerID)
    this.timerID = null
    this.storeRecordList()
  }

  addItem() {
    if (!modal.value) {
      alert('请输入正确的标题')
      return false
    }
    let recordList = this.state.recordList
    recordList.push(new Record(modal.value))
    this.setState({
      recordList: recordList
    })
    return true
  }

  switchModal() {
    modal.vivible = !modal.vivible
    modal.value = modal.vivible ? '' : modal.value
    this.setState({
      modal: modal
    })
  }

  handleInputChange(value) {
    modal.value = value
    this.setState({
      modal: modal
    })
  }

  loadeRecordList() {
    let tmp = Local.get(this.localKey) || []
    let records = []
    tmp.forEach(el => {
      records.push(new Record(el))
    })
    return records
  }

  storeRecordList() {
    Local.set(this.localKey, this.state.recordList)
  }

  render() {
    return (
      <section className="index">
        {/* 事项列表 */}
        <List
          renderHeader={() => {
            return <div className="index-list-header">我在做些什么？!</div>
          }}
          className="index-list"
        >
          {this.state.recordList.map((item, index) => (
            <CAction onDoubleTap={() => this.start(index)} key={index}>
              <Item extra={item.relativeStr} className={item.startTime && 'index-list-item-active'} onClick={() => {}}>
                <span className="index-list-item-title">{item.title}</span>
              </Item>
            </CAction>
          ))}
        </List>
        {/* 添加按钮 */}
        <div className="index-add float-btn" onClick={() => this.switchModal()}>
          <Icon type="cross" size="lg" color="#666" />
        </div>
        {/* 添加弹窗 */}
        <Modal
          visible={this.state.modal.vivible}
          transparent
          maskClosable={true}
          title="输入事项名称"
          footer={[
            {
              text: '确认',
              onPress: () => {
                if (this.addItem()) {
                  this.switchModal()
                }
              }
            }
          ]}
          onClose={() => {
            this.switchModal()
          }}
        >
          <div className="index-modal-input-box btn">
            <InputItem clear maxLength="30" onChange={value => this.handleInputChange(value)} />
          </div>
        </Modal>
      </section>
    )
  }
}

export default Index
