/**
 * 动作捕捉组件
 * @module /components/CAction
 * @example
 * // handlerDoubleTap
 * render() {
 *   return <CAction onDoubleTap={() => { this.handlerDoubleTap() }}>handlerDoubleTap</CAction>
 * }
 */
import React from 'react'

export default class CAction extends React.Component {
  componentDidMount() {}

  componentDidUpdate() {}

  componentWillUnmount() {}

  handlerClick() {
    let now = Date.now()
    if (now - this.time > 100 && now - this.time < 300) {
      this.time = 0
      this.props.onDoubleTap()
    } else {
      this.time = now
    }
  }

  render() {
    return (
      <div
        className="c-action"
        onClick={() => {
          this.handlerClick()
        }}
      >
        {this.props.children}
      </div>
    )
  }
}
